.review-page {
  background: #111;
  color: #fff;
  min-height: 100vh;
  padding-bottom: 40px;
  padding-left: 32px;
  padding-right: 32px;
  animation: page3dIn 1s cubic-bezier(0.23, 1, 0.32, 1);
  transform-style: preserve-3d;
}

.review-heading {
  margin-top: 80px;
  color: #ff69b4;
  font-size: 2.8rem;
  letter-spacing: 2px;
  text-align: center;
  animation: pinkGlow 1.2s ease-in-out infinite alternate;
}

@keyframes pinkGlow {
  0% { text-shadow: 0 0 8px #ff69b4, 0 0 16px #ff69b4; }
  100% { text-shadow: 0 0 24px #ff69b4, 0 0 48px #ff69b4; }
}

.review-subhead {
  text-align: center;
  font-size: 1.15rem;
  margin-bottom: 32px;
  color: #ffd700;
  letter-spacing: 1px;
  animation: fadeInUp 1.2s cubic-bezier(0.23, 1, 0.32, 1);
}

.review-list {
  display: flex;
  flex-wrap: wrap;
  gap: 32px;
  justify-content: center;
  margin-top: 24px;
}

.review-card {
  background: #181818;
  border-radius: 16px;
  box-shadow: 0 0 24px #ff69b4aa, 0 2px 12px #000a;
  padding: 32px 28px 24px 28px;
  max-width: 340px;
  min-width: 260px;
  display: flex;
  flex-direction: column;
  align-items: center;
  animation: fadeInUp 1.2s cubic-bezier(0.23, 1, 0.32, 1);
  border: 1.5px solid #ffd700;
  position: relative;
}

.review-stars {
  color: #ffd700;
  font-size: 1.4rem;
  margin-bottom: 12px;
  letter-spacing: 2px;
}

.review-text {
  font-size: 1.13rem;
  font-style: italic;
  color: #fff;
  margin-bottom: 18px;
  text-align: center;
}

.review-user {
  display: flex;
  align-items: center;
  gap: 12px;
}

.review-avatar {
  font-size: 2rem;
  color: #ff69b4;
  background: #222;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 0 12px #ff69b4;
}

.review-name {
  font-weight: bold;
  color: #ffd700;
  font-size: 1.08rem;
}

.review-role {
  font-size: 0.98rem;
  color: #ffb6d5;
}

@keyframes page3dIn {
  0% {
    opacity: 0;
    transform: rotateY(-40deg) scale(0.95);
  }
  100% {
    opacity: 1;
    transform: rotateY(0deg) scale(1);
  }
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(40px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
} 