.about-page {
  perspective: 1200px;
  animation: page3dIn 1s cubic-bezier(0.23, 1, 0.32, 1);
  transform-style: preserve-3d;
  background: #111;
  color: #fff;
  min-height: 100vh;
  padding-bottom: 40px;
  padding-left: 32px;
  padding-right: 32px;
}

.about-page h1 {
  margin-top: 80px;
  color: #ff69b4;
  font-size: 2.8rem;
  letter-spacing: 2px;
  text-align: center;
  animation: pinkGlow 1.2s ease-in-out infinite alternate;
}

@keyframes pinkGlow {
  0% { text-shadow: 0 0 8px #ff69b4, 0 0 16px #ff69b4; }
  100% { text-shadow: 0 0 24px #ff69b4, 0 0 48px #ff69b4; }
}

.about-page h2 {
  color: #ff69b4;
  margin-top: 36px;
  font-size: 1.6rem;
  letter-spacing: 1px;
  animation: fadeInLeft 1s cubic-bezier(0.23, 1, 0.32, 1);
}

.about-page p, .about-page ul {
  font-size: 1.1rem;
  line-height: 1.7;
  margin: 18px 0 0 0;
  animation: fadeInUp 1.2s cubic-bezier(0.23, 1, 0.32, 1);
}

.about-page ul {
  padding-left: 24px;
}

@keyframes page3dIn {
  0% {
    opacity: 0;
    transform: rotateY(40deg) scale(0.95);
  }
  100% {
    opacity: 1;
    transform: rotateY(0deg) scale(1);
  }
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(40px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInLeft {
  0% {
    opacity: 0;
    transform: translateX(-40px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
} 

.about-restaurant-img {
  display: block;
  margin: 32px auto 24px auto;
  max-width: 420px;
  width: 90%;
  border-radius: 18px;
  box-shadow: 0 0 32px 0 #ff69b4aa, 0 4px 32px 0 #000a;
  animation: fadeInUp 1.2s cubic-bezier(0.23, 1, 0.32, 1);
}

.about-chefs {
  display: flex;
  gap: 32px;
  justify-content: center;
  margin: 32px 0 24px 0;
  flex-wrap: wrap;
}

.about-chef-card {
  background: #181818;
  border-radius: 14px;
  box-shadow: 0 0 16px #ff69b4aa, 0 2px 12px #000a;
  padding: 18px 22px;
  text-align: center;
  max-width: 220px;
  animation: fadeInUp 1.2s cubic-bezier(0.23, 1, 0.32, 1);
}

.about-chef-img {
  width: 90px;
  height: 90px;
  object-fit: cover;
  border-radius: 50%;
  margin-bottom: 12px;
  border: 3px solid #ff69b4;
  box-shadow: 0 0 18px #ff69b4, 0 2px 8px #000a;
  animation: pinkGlow 1.2s ease-in-out infinite alternate;
} 